<template>
  <section class="hero">
    <div class="has-background-white">
      <div class="container has-text-left">
        <h2 class="title">
          <span v-html="dealer.nombre"></span>
        </h2>
      </div>
    </div>
    <div class="hero-body">
      <div class="container">
        <div class="columns card">
          <div class="column has-text-centered">
            <div id="dealer_map" class="has-text-left"></div>
          </div>
          <div class="column dealer-image has-text-left">
            <img :src="dealer.imagen" height="80">
            <h1 class="is-size-4 has-text-weight-semibold" v-html="dealer.nombre"></h1>
            <p><strong>Direccin:</strong> <a :href="'https://maps.google.com/?q='+dealer.direccion"><span v-html="dealer.direccion"></span></a></p>
            <p><strong>Telfono:</strong> <a :href="'call:'+ dealer.phone"><span v-html="dealer.telefono"></span></a></p>
            <p><strong>Email:</strong> <a :href="'mailto:'+ dealer.email"><span v-html="dealer.email"></span></a></p>
            <p><strong>Whatsapp:</strong> <a :href="'https://wa.me/'+dealer.whatsapp"><span v-html="dealer.whatsapp"></span></a></p>
            <p><strong>Horarios:</strong> {{ dealer.horarios | cs }} </p>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>

import axios from 'axios';
import moment from 'moment';
import helper from '@/components/Helper';

export default {
  name: 'Dealer',
  mounted: function(){
    if(!this.dealer.length) {
      var that = this
      axios.get( `/static/dummy/mercedes-dealers.json`)
        .then(res => {
          _.map(res.data, function(value, key) {
            if(value.slug === that.$route.params.slug){
              that.dealer = value
            }
          });
          that.initMap()
        }).catch(function(error) {
          console.log(error)
        })      
    }
  },
  methods: {
    initMap: function(){
      mapboxgl.accessToken = 'pk.eyJ1IjoibWFydGluZnJlZSIsImEiOiJjams4ZDh0dGoyanRwM3FtbHlqMXJnYjA3In0.VBD8U4yG46py1g9NxBFMPw'
      this.map = new mapboxgl.Map({
        container: 'dealer_map',
        style: 'mapbox://styles/mapbox/bright-v8'
      }); 

      console.log([this.dealer.lng,this.dealer.lat])
      var marker = new mapboxgl.Marker()
        .setLngLat([this.dealer.lng,this.dealer.lat])
        .addTo(this.map);

      this.map.setCenter([this.dealer.lng,this.dealer.lat])
      this.map.setZoom(15)
    }    
  },
  data () {
    return {
      dealer:[],
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>



